'use client';

import Link from 'next/link';
import Image from 'next/image';
import { useRouter, useSearchParams } from 'next/navigation';
import { useEffect, Suspense } from 'react';
import { AuthLayout } from '@/src/components/shared/AuthLayout';
import { InputField } from '@/src/components/forms/InputField';
import { Button } from '@/src/components/forms/Button';
import { SocialAuthDivider, SocialButton } from '@/src/components/forms/SocialAuthDivider';
import { useFormValidation } from '@/src/hooks/useFormValidation';
import { useToast } from '@/src/hooks/use-toast';
import { validators } from '@/src/lib/utils/validation';
import { login as loginApi } from '@/src/lib/auth/auth';
import { useAuth } from '@/src/context/AuthContext';
import { colors, spacing } from '@/src/styles/tokens';

const loginValidationSchema = {
  email: validators.email,
  password: (password: string) => {
    if (!password) return 'Password is required';
    return null;
  }
};

function LoginForm() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const { refreshUser } = useAuth();
  const { toast } = useToast();

  useEffect(() => {
    if (searchParams.get('expired') === 'true') {
      toast({
        variant: "destructive",
        title: "Session Expired",
        description: "Please log in again."
      });
    } else if (searchParams.get('logout') === 'true') {
      toast({
        title: "Logged Out",
        description: "Successfully logged out.",
        variant: "default"
      });
    }
  }, [searchParams, toast]);

  const { values, errors, isSubmitting, handleChange, handleSubmit, setFieldError } =
    useFormValidation(
      { email: '', password: '', rememberMe: false },
      async (formValues) => {
        try {
          const resp = await loginApi({
            email: String(formValues.email || ''),
            password: String(formValues.password || ''),
            rememberMe: Boolean(formValues.rememberMe)
          });

          if (resp?.access_token || resp?.token || (resp as any)?.user) {
            toast({
              title: "Welcome back!",
              description: "Login successful.",
              variant: "default"
            });
            await refreshUser();
            window.location.href = '/dashboard';
            return;
          }

          setFieldError('email', 'Invalid credentials');
          toast({
            variant: "destructive",
            title: "Login Failed",
            description: "Invalid email or password"
          });
        } catch (err: any) {
          const msg = err?.message || 'Login failed. Please try again.';
          setFieldError('email', msg);
          toast({
            variant: "destructive",
            title: "Error",
            description: msg
          });
        }
      },
      loginValidationSchema
    );

  return (
    <AuthLayout title="Sign In" subtitle="Welcome back to VaultString">
      <form onSubmit={handleSubmit} style={{ display: 'flex', flexDirection: 'column', gap: '20px' }}>
        
        {/* Email Field */}
        <div style={{ display: 'flex', flexDirection: 'column', gap: '8px' }}>
          <label style={{ fontSize: '12px', fontWeight: 'bold', color: colors.neutral[700], textTransform: 'uppercase', letterSpacing: '0.5px' }}>
            Email Address <span style={{ color: 'red' }}>*</span>
          </label>
          <InputField
            name="email"
            type="email"
            value={values.email}
            onChange={handleChange}
            error={errors.email}
            placeholder="you@example.com"
            required
            autoComplete="email"
          />
        </div>

        {/* Password Field */}
        <div style={{ display: 'flex', flexDirection: 'column', gap: '8px' }}>
          <label style={{ fontSize: '12px', fontWeight: 'bold', color: colors.neutral[700], textTransform: 'uppercase', letterSpacing: '0.5px' }}>
            Password <span style={{ color: 'red' }}>*</span>
          </label>
          <InputField
            name="password"
            type="password"
            value={values.password}
            onChange={handleChange}
            error={errors.password}
            placeholder="••••••••"
            required
            autoComplete="current-password"
          />
        </div>

        {/* Forgot Password Link */}
        <Link
          href="/reset-password"
          style={{
            fontSize: '14px',
            color: colors.primary.green,
            textDecoration: 'none',
            fontWeight: '500',
            alignSelf: 'flex-start'
          }}
        >
          Forgot password?
        </Link>

        {/* Remember Me */}
        <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
          <input
            type="checkbox"
            id="rememberMe"
            name="rememberMe"
            checked={values.rememberMe}
            onChange={handleChange}
            style={{
              accentColor: colors.primary.green,
              width: '18px',
              height: '18px',
              cursor: 'pointer'
            }}
          />
          <label 
            htmlFor="rememberMe" 
            style={{ fontSize: '14px', color: colors.neutral[600], cursor: 'pointer' }}
          >
            Remember me
          </label>
        </div>

        {/* Submit Button */}
        <div style={{ marginTop: '10px' }}>
          <Button type="submit" variant="primary" fullWidth isLoading={isSubmitting} style={{ padding: '12px', fontSize: '16px' }}>
            Sign In
          </Button>
        </div>

        <SocialAuthDivider />

        {/* Social Buttons Container */}
        <div style={{ display: 'flex', gap: '12px', width: '100%' }}>
          <div style={{ flex: 1 }}>
            <SocialButton 
              provider="google"
              onClick={() => console.log('Google login')} 
            />
          </div>
          <div style={{ flex: 1 }}>
            <SocialButton 
              provider="apple"
              onClick={() => console.log('Apple login')} 
            />
          </div>
        </div>

        {/* Footer */}
        <div style={{ textAlign: 'center', marginTop: '10px', fontSize: '14px', color: colors.neutral[600] }}>
          Don't have an account?{' '}
          <Link
            href="/signup"
            style={{
              color: colors.primary.green,
              fontWeight: '600',
              textDecoration: 'none',
            }}
          >
            Sign up
          </Link>
        </div>
      </form>
    </AuthLayout>
  );
}

export default function LoginPage() {
  return (
    <Suspense fallback={<div>Loading...</div>}>
      <LoginForm />
    </Suspense>
  );
}